
import Checklist from 'vux/src/components/checklist/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { ORDER_PAY } from '@/api/orderPay'
export default {
name: 'Pay',
data () {
return {
payMethods: ['微信支付', '支付宝支付'],
show: false,
price: 0
}
},
components: {
Checklist,
Radio,
Group,
Alert
},
methods: {
change (value) {
console.log(value)
},
async handlePay () {
const res = await ORDER_PAY({
orderId: this.$route.params.id
})
if (res.code === 200) {
this.show = true
}
},
handleHide () {
this.$router.push('/')
},
handleInit () {
const orderId = this.$route.params.id
const orderType = orderId.toString().slice(0, 1) === 'O' ? 'goodsOrder' : 'golfOrder'
const orders = JSON.parse(localStorage.getItem(orderType))
console.log('in')
orders.forEach(element => {
if (element.id === orderId) {
this.price = element.totalPrice
}
})
}
},
mounted () {
this.handleInit()
},
activated () {
this.handleInit()
}
}



